import React from 'react'
import { Result, Button, Descriptions, Image } from 'antd';
import { useSelector} from "react-redux"
export default function CreateSuccess({ maKhoaHoc, setNewCourse }) {
    const course = useSelector(state => state.Courses.hashListCourseAll[maKhoaHoc])
    function setNullForNewCourse() {
        setNewCourse(null)
    }
    return (
            <Result
                status="success"
                title={course?.tenKhoaHoc}
                subTitle={`Kha hc ${course?.tenKhoaHoc} c to thnh cng`}
            extra={
                <Descriptions
                    layout="vertical"
                    column={2}
                    bordered
                    size="middle"
                    extra={<Button type="default" size="middle" onClick={setNullForNewCourse}>To kha hc mi</Button>}
                >
                    <Descriptions.Item label="Tn kha hc">{course?.tenKhoaHoc}</Descriptions.Item>
                    <Descriptions.Item label="M kha hc">{course?.maKhoaHoc}</Descriptions.Item>
                    <Descriptions.Item label="Ch ">
                        {course?.danhMucKhoaHoc?.tenDanhMucKhoaHoc}
                    </Descriptions.Item>
                    <Descriptions.Item label="Ngy to">{ course?.ngayTao}</Descriptions.Item>
                    <Descriptions.Item label="M t" span={2}>{course?.moTa}</Descriptions.Item>
                    <Descriptions.Item label="Hnh nh">
                        <Image
                            src={course?.hinhAnh}
                            preview = {false}
                        />
                    </Descriptions.Item>
                </Descriptions>
            }
        />
  
       
    )
}
